						<h2><i></i>圣通平 硝苯地平缓释片(I) 10mg*45片*1袋/盒+感冒药一盒+退烧药一盒</h2>
						<div class="d_flex">
							<div class="price">
								<span>￥167</span>
								<h3 class="r_color">￥320.00</h3>
							</div>
							<div class="btn_union">
								<button>立即购买</button>
							</div>
						</div>
					</div>
				</div>
			</div> -->
			<!-- 套餐商品 有套餐时才显示 end -->
			<!-- 商品推荐 -->
			<div class="recommend">
