				<ul class="d_flex">
					<li v-for="pr in proRecommend" @click="jumpDetail(pr)">
						<img v-lazy="pr.productPhotos>0?pr.productPhotos[0].photoURL:''" :alt="pr.productName">
						<p>{{ pr.productName }}</p>
						<span>{{ pr.spec }}</span>
						<p><span class="r_color">￥{{ pr.ourPrice }}</span></p>
					</li>
				</ul>
			</div>
			<!-- 商品推荐 end -->
		 </swiper-slide>
		 <swiper-slide id="slideDetail">
		 	<div class="slide_detail">
		 		<!-- <div class="block-1">
		 			<h3>商品信息</h3>
		 			<ul>
		 				<li class="d_flex">
	 						<div class="left">品牌：</div>
	 						<div class="right">{{ productData.brandID | brandFilter(brandList) }}</div>
		 				</li>
		 				<li class="d_flex">
	 						<div class="left">批准文号：</div>
	 						<div class="right">{{ productData.productCategoryProperty?productData.productCategoryProperty.approval_number:'' }}</div>
		 				</li>
		 				<li class="d_flex">
