		 				</li>
		 				<li class="d_flex">
	 						<div class="left">英文名称：</div>
	 						<div class="right">{{ productData.productCategoryProperty?productData.productCategoryProperty.english_nm:'' }}</div>
		 				</li>
		 				<li class="d_flex">
	 						<div class="left">GMP认证：</div>
	 						<div 
	 							v-if="productData.productCategoryProperty" 
	 							class="right">{{ productData.productCategoryProperty.gmp_attestation_stats==1?'是':'否' }}
 							</div>
		 				</li>
		 			</ul>
		 		</div> -->
		 		<div class="block-2">
		 			<h3>产品展示</h3>
		 			<div class="block-2-imgs" v-html="productData.remark">
		 				<!-- 产品图片 -->
		 			</div>
		 		</div>
		 	</div>
		 </swiper-slide>
		 <swiper-slide  id="slideInstructions">
		 	<div class="slide_instructions" v-html="productData.instructions">
		 		<!-- 说明书 -->
		 	</div>
		 </swiper-slide>
		 <!-- Optional controls -->
		 <div class="swiper-pagination"  slot="pagination"></div>
		 <!-- <div class="swiper-button-prev" slot="button-prev"></div>
		 <div class="swiper-button-next" slot="button-next"></div> -->
		</swiper>
		<!-- 底部立即购买 -->
		<div class="options d_flex">
			<div class="left d_flex">
				<!-- <a href="http://vipwebchat.tq.cn/pageinfo.jsp?version=vip&admiuin=9815275&ltype=1&iscallback=0&page_templete_id=122543&is_message_sms=0&is_send_mail=0&action=acd&acd=1&type_code=123">在线咨询</a> -->
				<!-- <a id="tqchat2" href="javascript:void(0)">在线咨询</a> -->
				<a id="tqchat2" href="javascript:void(0)" title="在线咨询" style="cursor: pointer;">在线咨询</a>
				<a href="tel:400-9309-990">电话咨询</a>
				<a href="javascript:;" :class="{is_collect: productData.isCollect}" @click="collection(productData)">收藏</a>
			</div>
			<div class="right">
				<!-- OTC 按钮 、非药品类 -->
				<button v-if="productData.prescriptionType !== 1" @click="btnBuy">立即购买</button> 
				<!-- OTC 按钮 end -->
				<!-- 处方药按钮 -->
				<button v-if="productData.prescriptionType === 1" @click="btnBuy(2)">登记需求</button>
				<!-- 处方药按钮 end -->
			</div>
		</div>
