<script>

import { Field, Header, Button, TabContainer, TabContainerItem, Cell, Popup, Radio, Swipe, SwipeItem, Toast, MessageBox } from 'mint-ui'; 
import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
	name: 'product_detail',
	data (){
		return {
			isShowMore: false, // 是否显示更多
			curHeaderTitle: 0, // 当前的Tab 0-商品，1-详情，2-说明书
			isUnscRollable: false, //是否页面不可滚动
			backParentPage: false, //是否回到父页面
			swiperOption: {
		       // NotNextTick is a component's own property, and if notNextTick is set to true, the component will not instantiate the swiper through NextTick, which means you can get the swiper object the first time (if you need to use the get swiper object to do what Things, then this property must be true)
		       // notNextTick是一个组件自有属性，如果notNextTick设置为true，组件则不会通过NextTick来实例化swiper，也就意味着你可以在第一时间获取到swiper对象，假如你需要刚加载遍使用获取swiper对象来做什么事，那么这个属性一定要是true
		       notNextTick: true,
		       // swiper configs 所有的配置同swiper官方api配置
		       autoplay: 0,
		       autoHeight: true,//wrapper和container会随着当前slide的高度而发生变化。
		       // direction : 'vertical',
		       // effect:"coverflow",
		       loop: false,
		       grabCursor : true,
		       setWrapperSize :true,
		       // autoHeight: true,
		       // paginationType:"bullets",
		       pagination : {  
		       	el: '.title-swiper-pagination',
		        clickable: true,
		       	clickableClass: 'tcp',
		       	renderBullet: function (index, className) {
       	        	var headerTitle = [
						{ name: '商品', id: 0, },
						{ name: '详情', id: 1 },
						{ name: '说明书', id: 2 }
					];	 

		          return '<a class="' + className + '">' + headerTitle[index].name + '</a>';
		        },
		        pagination: 'swiper-pagination-bullet',
		        bulletActiveClass: 'my-bullet-active',
		        bulletActiveClass : 'cur',
		   	   },
		       paginationClickable :true,
		       // prevButton:'.swiper-button-prev',
		       // nextButton:'.swiper-button-next',
		       // scrollbar:'.swiper-scrollbar',
		       mousewheelControl : true,
		       observeParents:true,
		       // if you need use plugins in the swiper, you can config in here like this
		       // 如果自行设计了插件，那么插件的一些配置相关参数，也应该出现在这个对象中，如下debugger
		       // debugger: true,
		       // swiper callbacks
		       // swiper的各种回调函数也可以出现在这个对象中，和swiper官方一样
		       // onTransitionStart(swiper){
		       //   console.log(swiper)
		       // },
		       // more Swiper configs and callbacks...
		       // ...
		       on: {
		       	touchEnd: function(event){
		       		var index = this.activeIndex;
			      // console.log(document.body.scrollTop)
			      // console.log(document.body.clientHeight)
			    },
			    slideChange: function(){
			    	document.documentElement.scrollTop = document.body.scrollTop = window.scrollY = 0;
			    },
		       },
		    },
		    swiperOptionImg: {
		    	autoplay: 0,
		    	nested: true, //用于嵌套相同方向的swiper时，当切换到子swiper时停止父swiper的切换
		    	pagination: { //使用分页导航
		    		el: '.pro_imgs_pagination',
		    		clickable :true,
			  	},
		    },
		    windowHeight: '', //窗口高度
		    windowScrollHeight: '',// 商品页滚动多少到底部额高度
		    curSlideId: 'slideProduct' ,//当前slide的id
		    i: 0,
		    timer: null,
		    imgSrc: '',
		    productId: this.$route.query.proid, //产品ID
		    productData: '',//产品信息
		    curMultIndex: '',//当前的多规格
		    curMult: '',//当前多规格
		    num: 1,//数量
		    brandList: [],//品牌列表
		    proRecommend: [],//商品推荐
		}
	},
	components: {
		'mt-field': Field,
		'mt-header': Header,
		'mt-button': Button,
	    'mt-tab-container': TabContainer,
	    'mt-tab-container-item': TabContainerItem,
    	'mt-cell': Cell,
    	'mt-popup': Popup,
    	'mt-radio': Radio,
	    swiper,
	    swiperSlide,
	    'mt-swipe': Swipe,
	    'mt-swipe-item': SwipeItem,
	},
	filters: {
		// 图片类型 处方,OTC甲,OTC乙
		imgType ( data ){
			if( data == 1 ){
				return 'RX';
			}else if(data == 2){
				return 'OTC-2'
			}else if(data == 3){
				return 'OTC-1'
			}
		},
	    //图片地址筛选
	    photoFilter ( data ){
	      if(data.productPhotos.length <= 0){
	        return '../../../static/images/ak.png';
	      }else{
	        return data.productPhotos[0].photoURL;
	      }
	    },
		//品牌过滤
		brandFilter ( data, brandList ){
			for ( let i in brandList ){
				if( brandList[i].brandID == data ){
					return brandList[i].brandName;
				}
			}
		},
		//价格
		priceFilter ( data ){
			return '￥' + Number(data).toFixed(2);
		}
	},
	watch: {
		num (){
			var checkNum = /^[0-9]*[1-9][0-9]*$/;

			if( !checkNum.test(this.num) ){
				this.$options.methods.showToast('请填写正确的数量');
				return false;
			}
		},
	},
	computed: {
		// 得到当前的swiper对象，同时notNextTick必须为true
		swiper (){
			 //获取 ref="mySwiper" 的swiper
		 return this.$refs.mySwiper.swiper ;
		},
		activeIndexSlide(){
			return this.$refs.mySwiper.swiper.activeIndex ;
		},
		//产品图片
		showImg (){
			if(this.$data.productData){
		    	if( this.$data.productData.productPhotos.length >0 ){
		    		return 1
		    	}else{
		    		return 0;
		    	}
			}
		},
	},
	created (){
		const _self = this;
	    var myDate = new Date();//获取系统当前时间
	    var y = myDate.getFullYear();
	    var m = myDate.getMonth() + 1;
	    if( m < 10 ){
	      m = '0'+m;
	    }
	    var d = myDate.getDate();
	    if( d < 10 ){
	      d = '0'+d;
	    }
	    var datas = y + '-' + m + '-' + d + ' ' + '23:59:59';

	    //判断是哪个端
		var sUserAgent = navigator.userAgent.toLowerCase();
		var bIsIpad = sUserAgent.match(/ipad/i) == "ipad";
		var bIsIphoneOs = sUserAgent.match(/iphone os/i) == "iphone os";
		var bIsMidp = sUserAgent.match(/midp/i) == "midp";
		var bIsUc7 = sUserAgent.match(/rv:1.2.3.4/i) == "rv:1.2.3.4";
		var bIsUc = sUserAgent.match(/ucweb/i) == "ucweb";
		var bIsAndroid = sUserAgent.match(/android/i) == "android";
		var bIsCE = sUserAgent.match(/windows ce/i) == "windows ce";
		var bIsWM = sUserAgent.match(/windows mobile/i) == "windows mobile";

		if (!(bIsIpad || bIsIphoneOs || bIsMidp || bIsUc7 || bIsUc || bIsAndroid || bIsCE || bIsWM) ){
			// productId
			//window.location.href = "http://192.168.2.250/productDetails.html?productId=" + this.productId;
		}

	    //判断是哪个端 end
	    
	    //GA 'UA-108545486-2'  'UA-123344424-2'
	    gtag('config', 'UA-108545486-2', {
		  'page_title' : '产品详情页-' + this.productData.productName,
		  'page_path': this.$route.fullPath
		});
	    gtag('config', 'UA-123344424-2', {
		  'page_title' : '产品详情页-' + this.productData.productName,
		  'page_path': this.$route.fullPath
		});

		//获品牌
		this.$ajax({
			url: _self.webRoot + '/brand/getBrandList'
			// url: '/brand/getBrandList'
		})
		.then(function(res){
			// console.log(res)
			if(!res.data.success){
				_self.$options.methods.showToast(res.data.msg);
				return false;
			}
			_self.brandList = res.data.data;
		})
		.catch(function(err){
			console.log(err)
		});

		//获取产品信息
		this.$ajax({
			url: _self.webRoot + '/product/getProductByProductId',
			// url: '/product/getProductByProductId',
			data: {
				productId: _self.productId,
				memberId: _self.$store.state.memberId,
			}
		})
		.then(function(res){
			// console.log('产品信息：', res)
			if( !res.data.data ){
				_self.$options.methods.showToast('没有商品信息!', 1500);
				setTimeout(function(){
					// _self.$router.go(-1);
					_self.$router.replace({path: '/index'});
				},1500);
				return false;
			}
			if( res.data.success ){
				if( !res.data.data ){
					_self.$options.methods.showToast(res.data.msg);
					return false;
				}
				_self.productData = res.data.data;
				// _self.imgSrc = '/apijs' + res.data.data.photodURL;
				_self.imgSrc = _self.webRoot + res.data.data.photodURL;
			}else{
				_self.$options.methods.showToast(res.data.msg);
			}
		})
		.catch(function(err){
			console.log(err)
		});

		//获取商品推荐
    	this.$options.methods.getProRecom( _self, datas, 'proRecommend', datas, _self.proRecommend, '商品推荐无数据' );
	},
	mounted (){
		this.$emit('changeClass',false);
		// console.log(this.swiper.slideTo(3, 1000, false))
		
		//监听滚动 切换tab页面
		window.addEventListener('scroll',this.changePage);

		//创建 TQ聊天工具
		//先清除
		var childScript = document.getElementById('app').getElementsByTagName("script");
		for( let i=0; i<childScript.length; i++ ){
			document.getElementById('app').removeChild(childScript[i]);
		}

		//增加
		var src1 = document.createElement('script')
		var scr = document.createElement('script');
		
		src1.src = "http://float2006.tq.cn/floatcard?adminid=9815275&sort=2";
		scr.innerText='var tqUserDefined = {openWinType:3};TQ_RQF = 6;TQ_RQC = 2;TQKF.Binding("tqchat1","acd","","123");TQKF.Binding("tqchat2","acd","","123");';
		
		document.getElementById('app').appendChild(src1);
		document.getElementById('app').appendChild(scr);
		//创建 TQ聊天工具 end
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	methods: {
		//选择多规格
		chooseMultiSpec (pd,index){
			this.curMultIndex = index;
			this.curMult = pd;
		},
		//数量减少
		numReduce (){
			const _self = this;
			var checkNum = /^[0-9]*[1-9][0-9]*$/;
			if( this.num <= 1 ){
				return false;
			}else{
				if( !checkNum.test(this.num) ){
					this.$options.methods.showToast('请填写正确的数量');
					return false;
				}
				this.num--;
			}
		},
		//数量增加
		numAdd (){
			const _self = this;
			var checkNum = /^[0-9]*[1-9][0-9]*$/;
			if( this.num === 0 || !this.num ){
				this.num = 1;
			}else{
				if( !checkNum.test(this.num) ){
					this.$options.methods.showToast('请填写正确的数量');
					return false;
				}
				this.num++;
			}
		},
	    // 跳到商品详情页
	    jumpDetail ( item ){
	      this.$router.push({ path: 'product_detail', query: {proid: item.productID} });
	    },
		//
		changePage: function (){
			return false; //暂不开放 -- 有待优化
			const _self = this;
			//获取窗口可视高度
			var windowHeight = document.documentElement.clientHeight || document.body.clientHeight ;
			//获取商品页的高度
			var slideHeight = document.getElementById(_self.curSlideId).offsetHeight ;
			//商品页滚动多少到底部额高度
			_self.windowScrollHeight = slideHeight - windowHeight;

			clearTimeout(_self.timer);
			_self.timer = setTimeout(function(){ 

			var scroll_top = document.documentElement.scrollTop;

				if( scroll_top >= _self.windowScrollHeight ){
					// console.log(activeIndex);
					var activeIndex = _self.swiper.realIndex >= 2 ? 2 : _self.swiper.realIndex+1 ;

					_self.curSlideId = _self.swiper.slides[activeIndex].getAttribute('id');

					if( _self.swiper.realIndex >= 2 ){ // 到达最后一页
						return false;
					}
					//切换到下一页
					_self.swiper.slideNext(300);
					// console.log('ddddd: ',_self.curSlideId)
					return false;
				}else if( scroll_top == 0 ){
					var activeIndex = _self.swiper.realIndex <= 0 ? 0 : _self.swiper.realIndex-1 ;

					_self.curSlideId = _self.swiper.slides[activeIndex].getAttribute('id');
					if( _self.swiper.realIndex <= 0 ){ // 到达第一页
						return false;
					}
					//切换到上一页
					_self.swiper.slidePrev(300);
				}
			},100);
			// console.log('aaa:', scroll_top)
			// console.log('aaa:', this.swiper.height);
		},
		btnBuy ( id ){
			const _self = this;
			
			// 处方药
			if( id == 2 ){
				this.$router.push({ path: '/toregister', query: { id: this.productId, num: this.num, curMultIndex: this.curMultIndex } });
				return false;
			}
			// console.log(this.curMult)
			// return false;
			
			// OTC
			var productNumber = '';
			if( this.productData.isMultSpec ){ //开启多规格
				productNumber = this.curMult.detailCodeID; //是

				if( !productNumber ){
					_self.$options.methods.showToast( '请选择规格!' );
					_self.flag = false;
					return false;
				}
			}

			var proInfo = {
				orderId: this.productId,//产品ID
				num: this.num,//数量
				specId: productNumber,//规格ID 单规格不用传
				specName: this.curMult.propertyAndValue,//规格名称 单规格不用传
				proNmae: this.productData.productName,//商品名称
			};
			
			this.$store.commit('addBuyProductInfo', proInfo);
			sessionStorage.addBuyProductInfo = JSON.stringify( proInfo );
			// console.log(_self.$store.state.buyProductInfo);
			this.$router.push({ path: '/purchase'});
		},
		// 点击切换silde tab
		jumpSildePage ( id ){
			this.curHeaderTitle = id;
			this.swiper.slideTo( id, 500, false );
		},
		//确定按钮
		btnSave (){	
			console.log('点击确定')
		},
		//收藏
		collection ( data ){
			const _self = this;
			if( !_self.$store.state.memberId ){
				// _self.$options.methods.showToast('请先登录');
				MessageBox.confirm('请先登录').then(action => {
		            _self.$router.push({
		                path: '/login',
		                query: {redirect: 'pdt'}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
		            })
				}).catch( cancel => {
					console.log(cancel)
				} );
				return false;
			}
			// console.log(data)
			if( this.flag ){return}
			//已收藏
			if( data.isCollect ){
				this.$ajax({
					url: _self.webRoot + '/favorite/delFavoriteBymemberIdAndProId',
					data: {
						memberId: _self.$store.state.memberId,
						productId: data.productID
					}
				})
				.then(function(res){
					console.log(res)
					if( res.data.success ){
						_self.productData.isCollect = false;
					}
					_self.$options.methods.showToast(res.data.msg);
					_self.flag = false;
				})
				.catch(function(err){
					console.log(err)
				})
				return false;
			}

			//收藏
			this.$ajax({
				url: _self.webRoot + '/favorite/addFavorite',
				data: {
					productID: _self.productId,
					memberID: _self.$store.state.memberId,
					sysNo: 'WAP端'
				}
			})
			.then(function(res){
				// console.log('收藏: ',res)
				if( res.data.success ){
					_self.productData.isCollect = true;
				}
				_self.$options.methods.showToast(res.data.msg);
				_self.flag = false;
			})
			.catch(function(err){
				console.log(err)
				_self.flag = false;
			});
		},

		//公共函数
		// 后退
		goBack (){
			this.$store.commit('addBuyProductInfo', '');//清除信息
			this.$store.commit('addBbuyProductAdd', '');//清除信息
			sessionStorage.removeItem('addBuyProductInfo');
			sessionStorage.removeItem('addBbuyProductAdd')
			this.$router.go(-1);
		},
	    //显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	      }
	    },
		//显示更多
		showMore (){
			this.isShowMore = true;
		},
        // 获取模块数据
	    getProRecom ( _self, endDate1, pageNo, endDate2, moduleData, errMsg ){

	      _self.$ajax({
	        url: _self.webRoot + '/seachSt',
	        // url: '/seachSt',
	        data: {
	          endDate: endDate1,
	          pageNo: pageNo,
	          isValid: 1
	        }
	      })
	      .then(function(res){
			// console.log('res: ', res)
			if(res.data.data.length <= 0){return}
	        var showId = res.data.data[0].showID;

	        if( res.data.success ){
	          _self.$ajax({
	            url: _self.webRoot + '/seachStDetail',
	            // url: '/seachStDetail',
	            data: {
	              endDate: endDate2,
	              showID: showId,
	              isValid: 1
	            }
	          })
	          .then(function(res){
	            // console.log('商品推荐: ',res.data)
	            if( res.data.success && res.data.length>0 ){
	              moduleData.splice(0); //先移除数据
	              for (var i = 0; i < res.data.data.length; i++) {
	                var productId = res.data.data[i].productID;
	                _self.$ajax({
	                  url: _self.webRoot + '/product/getProductByProductId',
	                  // url: '/product/getProductByProductId',
	                  data: {
	                    productId: productId,
	                    memberId: _self.$store.state.memberId,
	                  }
	                })
	                .then(function(res){
	                  if( res.data.success && res.data.data ){
	                    moduleData.push(res.data.data);
	                  }
	                  if( !res.data.success ){
	                    moduleData = [];
	                    console.log(errMsg)
	                  }
	                })
	                .catch(function(err){
	                  console.log(err)
	                });
	              }
	            }else{
	              moduleData = [];
	              console.log(errMsg)
	            }
	          })
	          .catch(function(err){
	            console.log(err)
	          });
	        }else{
	          moduleData = [];
	          console.log(errMsg)
	        }
	      })
	      .catch(function(err){
	        console.log(err)
	      });
	    },

	},
}	
</script>

<style scoped>
@import '../../../static/css/headerTitle.css';
@import './Css/product_detail.css';
</style>
<style>
.Product_detail_wrap .mint-header.is-fixed {
	z-index: 2;
	width: 100%;
	max-width: 640px;
	left: 50%;
	transform: translateX(-50%);
	margin:0;
}
.Product_detail_wrap .mint-header .mint-header-button {
	width: 1.1875rem;
	flex: inherit;
}
.Product_detail_wrap .mint-header .mint-header-title {
	width: 7.0rem;
	flex: inherit;
	text-align: center;
	font-size: 0;
	height: 1.25rem;
	line-height: 1.25rem ;
}
.Product_detail_wrap .mint-header .mint-header-title a {
	display: inline-block;
	font-size: 0.4375rem;
	/* padding:0 0.4375rem;
	margin: 0 0.46875rem; */
	width: 1.8125rem;
	height: 1.25rem;
	border-radius: 0;
	background-color: transparent;
	color: #262626;
	position: relative;
	opacity: 1;
	outline: none;
}
.Product_detail_wrap .mint-header .mint-header-title a.cur {
	color: #0f904d;
}
.Product_detail_wrap .mint-header .mint-header-title a.cur::after {
	content: '';
	position: absolute;
	width: 100%;
	height: 0.03125rem;
	bottom: 0.03125rem;
	left: 0;
	background-color: #0f904d;	
}
.Product_detail_wrap .mint-header .swiper-pagination {
	position: static;
	height: 1.25rem;
}

.Product_detail_wrap .pro_imgs_pagination .swiper-pagination-bullet {
	margin: 0 0.15625rem;
	background-color: #909090;
	width: 0.3125rem;
	height: 0.3125rem;
	border-radius: 50%;
	outline:none;
}
.Product_detail_wrap .pro_imgs_pagination .swiper-pagination-bullet-active {
	background-color: #767676;
}


.Product_detail_wrap .union .mint-cell-wrapper {
	border-bottom: 0;
}
.Product_detail_wrap .base_info>span>a {
	display: inline-block;
	color:#fd2c17;
}
</style>


